import Swiper, { Navigation, Thumbs, Zoom } from 'swiper';

// configure Swiper to use modules
Swiper.use([Navigation, Thumbs, Zoom]);

let galleryThumbs = new Swiper('.patents-selection .swiper-container', {
  // Optional parameters
  //loop: true,
  freeMode: true,
  watchSlidesVisibility: true,
  watchSlidesProgress: true,
  slidesPerView: 'auto',
  centerInsufficientSlides: true,
});

let galleryTop = new Swiper('.patent-image .swiper-container', {
  // Optional parameters
  //loop: true,
  zoom: true,
  slidesPerView: 1,
  thumbs: {
    swiper: galleryThumbs,
  },
  threshold: 2,

  // Navigation arrows
  /*navigation: {
    nextEl: '.swiper-button-next',
    prevEl: '.swiper-button-prev',
  },*/
});